.subscriptionPlansPage {
  padding: 24px;
  display: flex;
  flex-direction: column;
}

@media (min-width: 992px) {
  .subscriptionPlansPage {
    padding: 40px;
  }
}

.title {
  margin-bottom: 24px;
  font-size: 24px;
  text-transform: uppercase;
}

.tiles {
  margin: -16px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.tile {
  margin: 16px;
  width: 100%;
}

@media (min-width: 768px) {
  .tile {
    max-width: 312px;
  }
}

.enterpriseTile {
  align-self: center;
  margin-top: 32px;
  padding: 24px 32px;
  font-size: 20px;
  font-weight: 400;
  color: #313a58;
  background-color: #cece84;
  border-radius: 4px;
  cursor: pointer;
}
