.subscriptionPlanTile {
  background-color: #ffffff;
  border-radius: 4px;
  box-shadow: 0px 3px 6px #00000029;
  overflow: hidden;
}

.name {
  padding: 0 32px;
  line-height: 80px;
  font-size: 24px;
  font-weight: 400;
  text-transform: capitalize;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: #242b43;
  box-shadow: 0px 3px 6px #00000029;
}

.price {
  padding: 16px 32px;
  box-shadow: 0px 3px 6px #00000029;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  & > * {
    &:nth-child(1) {
      position: relative;
      font-size: 56px;
      font-weight: 700;
      color: #ef336a;
      & > * {
        position: absolute;
        right: 4px;
        bottom: -12px;
        font-size: 16px;
      }
    }
    &:nth-child(2) {
      font-size: 11px;
      color: #242b43;
    }
  }
}

.priceBilledAnually {
  padding: 16px 32px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  & > * {
    &:nth-child(1) {
      position: relative;
      font-size: 32px;
      font-weight: 700;
      color: #ef336a;
      & > * {
        position: absolute;
        right: 2px;
        bottom: -10px;
        font-size: 12px;
      }
    }
    &:nth-child(2) {
      font-size: 11px;
      color: #242b43;
    }
  }
}

.feature {
  padding: 0 32px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 32px;
  font-size: 14px;
  & > * {
    &:nth-child(1) {
      font-weight: 400;
      color: #242b43;
    }
    &:nth-child(2) {
      font-weight: 900;
      color: #315845;
    }
  }
}

.widgets {
  height: 80px;
  margin-left: 32px;
  margin-right: 32px;
  overflow-y: auto;

  &::-webkit-scrollbar {
    width: 8px;
  }

  &::-webkit-scrollbar-track {
    background-color: rgba(0, 0, 0, 0.025);
  }

  &::-webkit-scrollbar-thumb {
    border: none;
    background-color: rgba(0, 0, 0, 0.1);
    border-radius: 0;
  }
}

.widget {
  height: 20px;
  line-height: 20px;
  font-size: 12px;
  color: #242b43;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.bottom {
  margin-top: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 56px;
  font-size: 20px;
  font-weight: 400;
  color: #242b43;
}

.button {
  width: 100%;
  height: 100%;
  font-size: inherit;
  color: white;
  background-color: #458164;
  box-shadow: 0px 3px 6px #00000029;
}
